import React, { useEffect } from 'react'
import { uniswapIframeUrl } from '../../config/constants/contants'


function adjustIframeHeight(event) {
  const iframe = document.querySelector('#mappingfunk-uniswap-widgets');
  if (event.data.iframeHeight && event.data.iframeSrc && event.data.id) {
    if (iframe) {
      iframe.setAttribute('style', `width: 100%;height: ${event.data.iframeHeight + 24}px`);
    }
  }
}

export default function BuyToken() {


  useEffect(() => {
    window.addEventListener("message", adjustIframeHeight, false);
    return () => {
      window.removeEventListener("message", adjustIframeHeight, false);
    }
  }, [])

  return (
    // eslint-disable-next-line jsx-a11y/iframe-has-title
    <iframe id="mappingfunk-uniswap-widgets" style={{ width: 800, height: '500px' }} src={uniswapIframeUrl} frameBorder="0" />
  )
}
